html {
  scroll-behavior: smooth;
}

body {
  font-family: $mplus;
  margin: 0;
}

a {
  text-decoration: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0 0 0 0);
}

@media (max-width: $tablet) {
  .nav-opened {
    overflow: hidden;
  }
}
